.skills-section {
    /* Dark background similar to your image */
    padding: 4rem 0;
    overflow: hidden;
    /* Crucial to hide the overflowing items for the animation */
    width: 100%;
    box-sizing: border-box;
}

.skills-carousel-container {
    overflow: hidden;
    width: 100%;
}

.skills-carousel-track {
    display: flex;
    /* Use max-content to allow the content to dictate its width,
     which will be the combined width of all duplicated items. */
    width: max-content;

    /* The magic for the infinite loop */
    animation: slideSkills 60s linear infinite;
    /* Adjust duration (e.g., 30s) as needed for speed */
}

/* Ensure skill items don't shrink and have consistent spacing */
.skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    margin: 0 1.5rem;
    /* Adjust spacing between items */
    text-align: center;
    min-width: 120px;
    /* IMPORTANT: Give items a fixed width or min-width */
    flex-shrink: 0;
    /* Prevent items from shrinking */
    color: black;
    transition: background-color 0.3s ease, color 0.3s ease;
    /* For hover effects */
}

/* Hover effects (from previous response) */

.skill-icon {
    width: 50px;
    height: 50px;
    margin-bottom: 0.5rem;
    transition: filter 0.3s ease;
}



/* KEYFRAMES FOR THE INFINITE SLIDE */
@keyframes slideSkills {
    0% {
        transform: translateX(0%);
    }

    100% {
        /*
      This is the critical part for the infinite loop.
      If you duplicate your `skillsData` array once (e.g., `[...skillsData, ...skillsData]`),
      then the `skills-carousel-track` contains two full sets of skills.
      To loop seamlessly, you need to translate by exactly 50% of the track's total width,
      which corresponds to the width of one original set of skills.
    */
        transform: translateX(-50%);
    }
}