.events li {
  display: flex;
  color: var(--clr-opp);
}

.events time {
  position: relative;
  padding: 0 .1em;
  width: 120px;
  font-size: large;
  font-family: "Poppins";
}

.time-text {
  margin-top: -0.35em;
}

.events time::after {
  content: "";
  position: absolute;
  z-index: 1;
  right: 0;
  top: 0;
  transform: translateX(54%);
  border-radius: 50%;
  background: var(--clr-primary);
  border: 2px var(--clr-primary) solid;
  width: .8em;
  height: .8em;
}


.spanF {
  padding: 0 1.5em 1.5em 1.5em;
  position: relative;
  width: 85%;
  height: 8em;
  font-family: "Poppins";
  font-size: 16px;
  color: var(--clr-fg);
}

time {
  color: var(--clr-fg);
}

.spanF::before {
  content: "";
  position: absolute;
  z-index: 1;
  left: 0;
  height: 100%;
  border-left: 2px var(--clr-primary) solid;
  bottom: 120px;
}

.events strong {
  display: block;
  font-weight: bolder;
  font-family: "Poppins";
  font-size: 18px;
  margin-top: -0.35em;
}

.about__desc2 {
  font-size: 1rem;
  margin-top: 2.4em;
  text-align: center;
}


.events {
  margin: 1em auto;
  width: max-content;
  ;
  font-family: "Poppins";
  align-self: center;
}

.events,
.events *::before,
.events *::after {
  box-sizing: border-box;
  font-family: arial;
}

.events {
  margin-top: 60px;
}

.spanA::before {
  height: 50%;

}

