.roadmap_container {
    padding-inline: 1rem;
    max-width: 1100px;
    margin-inline: auto;
    margin-top: 5rem;
}

.roadmap {
    padding-block: 2rem;
}

.roadmap h1 {
    margin-bottom: 1rem;
    text-align: center;
    font-size: 2.2rem;
}

.roadmap__timeline {
    position: relative;
}

.roadmap__line {
    position: absolute;
    top: 0;
    left: 0.35rem;
    height: 100%;
    width: 2px;
    z-index: 100;
    background-color: var(--clr-primary);
}

.roadmap-item {
    margin-bottom: 2rem;
    position: relative;
}

.roadmap-item__marker {
    position: absolute;
    left: -0.2rem;
    z-index: 100;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: var(--clr-primary);
}

.roadmap-item__content {
    box-shadow: var(--shadow2);
    transition: transform 0.2s linear;
    padding: 1rem;
    margin-left: 3rem;
    border-radius: 0.5rem;
    max-width: 100%;
}

.roadmap-item__stage {
    font-size: 1.15rem;
    margin-bottom: 0.5rem;
}

.roadmap-item__position {
    font-size: 1rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

.roadmap-item__date {
    font-size: 0.95rem;
    margin-bottom: 1rem;
    font-weight: 500;
    font-style: italic;
}

.roadmap-item__detail {
    margin-top: 0.5rem;
}

.section__title {
    text-align: center;
    margin-bottom: 1em;
    text-transform: uppercase;
}

/* Media query */
@media (min-width: 768px) {
    .roadmap__line {
        left: 52%;
    }

    .roadmap-item__marker {
        left: 51.152%;
    }



    .roadmap-item--odd {
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        flex-direction: row-reverse;
    }

    .roadmap-item--odd .roadmap-item__content {
        margin-left: 0;
        width: 45%;
    }

    .roadmap-item--even .roadmap-item__content {
        margin-left: 50px;
        width: 45%;
    }
}