.about {
  flex-direction: column;
  margin-top: 3em;
  align-items: center;
}

.education {
  flex: 1
}

.about__name {
  color: var(--clr-primary);
  font-size: 35px;
}

.timeline__color {
  color: var(--clr-primary);
}

.about__role {
  margin-top: 1.2em;
}

.pointer {
  cursor: pointer;
}

.about__desc {
  font-size: 1rem;
  max-width: 600px;
}

.about__desc,
.about__contact {
  margin-top: 2.4em;
}

.about .link--icon {
  margin-right: 0.8em;
}

.about .link--desc {
  margin-right: 0;
  color: var(--clr-fg);
}

.about .btn--outline {
  margin-right: 1em;
}


@media (max-width: 600px) {
  .app .about {
    align-items: flex-start;
    margin-top: 2em;
  }
}