.skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    margin: 0 1.5rem;
    /* Adjust spacing between items */
    text-align: center;
    min-width: 120px;
    /* Ensure items have a consistent width */
    flex-shrink: 0;
    /* Prevent items from shrinking */
    color: black;
    /* Or your preferred text color */
    transition: background-color 0.3s ease, color 0.3s ease;
}

.skill-icon {
    width: 50px;
    /* Adjust icon size */
    height: 50px;
    margin-bottom: 0.5rem;
    margin: 10px;
}

.skill-name {
    font-size: 1rem;
    font-weight: 500;
}