.header {
  height: 8em;
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
  justify-content: space-between;
}
.image-me {
    position: relative;
}  
.image-me:after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
  }
@media (max-width: 600px) {
  .header {
    height: 6em;
  }
}
